* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}

.nav{
background-color: rgb(25, 25, 25);
width:100%;
height:5%;
position:fixed;
z-index: 99;
display:flex;
flex-direction: row-reverse;
align-items: center;
}
@media screen and (max-width:1024px){
    .nav{
        height:5vh;
    }
}
.ul{
    display:flex;
    justify-content: center;
    float:right;
    align-content:center;
    list-style-type: none;
    padding-right:100px;
  
    
}

.li{
    margin-left:1rem;
    margin-right:1rem;
    color:aliceblue;
    font-family:"Kode Mono";
    font-size: 1.13rem;
    font-weight:100;

}
@media screen and (max-width:1024px){
    .li{
        
    }
}

@media screen and (max-width:900px) and (min-height:1000px) {
    .li{
      font-size: 1.8rem;
    }
  }

.li:hover{
    cursor:pointer;
}

.active{
    /*
    background-image: linear-gradient(45deg, rgb(99, 98, 95), rgb(234, 229, 230));
    background-clip: text;
    -webkit-text-fill-color: transparent;
    */
    border-bottom: 0.1rem aliceblue solid;
}
@media screen and (max-width:900px) and (min-height:1000px) {
    .active{
        border-bottom: 0.15rem aliceblue solid;
    }
  }
.bars{
    color:aliceblue;
    font-size:3vw;
    float:right;
    padding-right:2vw;
    font-size:1.4rem;
    padding-top:.8vw;
}

@media screen and (max-height: 412px) and (orientation: landscape) {
    .bars {
        font-size:2.5vw;
    }
  }


.bars:hover{
    cursor: pointer;
}
.mobilemenu{
    width: 100%;
    max-width:100vw;
    height:100vh;
    z-index:999;
    background-color:black;
    color:aliceblue;
    font-family:"Kode Mono";
    position: fixed;
    display: flex;
    flex-direction: column;
    align-items: center;

    
}

@media screen and (min-width:769px){
    .mobilemenu{
        visibility: hidden;
        height: 0;
        width:0;
    }
}
.hidden{
    visibility: hidden;
    height:0;
    width:0;
}

.mobilenav{
    width: 80%;
position: absolute;
    top:0;

    height:5rem;
    display: flex;
    align-items: center;
    z-index: 9999;
}

.closediv{
    position: absolute;
    right:0;
    font-size:2rem;
    height:2rem;
    width:2rem;

}

.closediv:hover{
    cursor: pointer;
}
.h1{
    height:2rem;
    left:0;
    font-size:1.5rem;

    width:20rem;
}

.linkbox{
    height:20rem;
    width:80%;

    font-size: 2rem;
    position: absolute;
    top:5rem;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: space-evenly;
}

.link{
    width: 100%;
    left:0;
    
}

.link:hover{
    cursor: pointer;
}

.mobileactive{
    border-left:.15rem solid aliceblue;
}