.container {
  width: 100%;
  display: flex;
  flex-direction: row;
  position: relative;
  justify-content: center;
  align-items: center;
  overflow: hidden;

}

.h1 {
  color: aliceblue;
  font-size: calc(20px + 1.5vw);
  font-family: "Kode Mono";
  position: absolute;
  top: calc(70px + 0.7vw);
  height: 15%;

  animation: top-down 0.75s;
}

@media screen and (max-width:768px) {
  .h1{
    top:7%;
  }
}


@media screen and (max-width:440px) and (min-height:742px) {
  .h1{
   top:9%;
  }
}
@media screen and (max-height: 440px) and (orientation: landscape) {
  .h1{
   top:5%;
  }
}


.formcontainer {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  width: 80%;
  height: 81%;
  animation: 1fade-in 0.75s;
  font-family: sans-serif;
  margin-top: 2.6%;

}

@media screen and (max-width:500px) {
  .formcontainer{
    margin-top:-10%;
  }
}

::placeholder{
  color:aliceblue;
  opacity:1;
}
input:-webkit-autofill,
input:-webkit-autofill:focus, 
input:-webkit-autofill:active  {
    -webkit-box-shadow: 0 0 0 30px rgb(25, 25, 25) inset !important;
    color: aliceblue!important; /* Change this to your desired text color */
}

input:-webkit-autofill:hover {
  -webkit-box-shadow: 0 0 0 30px rgb(25, 25, 25) inset !important;
}

input:-webkit-autofill:hover::first-line{
    color:aliceblue !important; /* Change this to your desired text color */
}


input:-webkit-autofill::first-line {
    color:aliceblue !important; /* Change this to your desired text color */
}

.form {
  height: 100%;
  width: 100%;

  display: flex;
  justify-content: center;
  flex-direction: column;
}

@media screen and (max-width:500px) {
  .form{
    gap:2vh;
  }
}


@media screen and (max-width:440px) and (min-height:742px) {
  .form{
    justify-content: space-evenly;
    height:80%;
  }
}

input[type="text"] {
  font-size: calc(10px + 0.35vw );
  text-indent: calc(0px + 1.1vw);
}

input[type="email"] {
  font-size: calc(10px + 0.35vw );
  text-indent: calc(0px + 1.1vw);
}

textarea[type="message"]{
  font-size: calc(10px + 0.35vw );
  text-indent: calc(13px + 1.5vw);
}

.firstlastcontainer {
  display: flex;
  flex-direction: row;
  width: 100%;
  height: 13%;
  justify-content: space-between;
}

@media screen and (max-width: 500px) {
  .firstlastcontainer {
    height: 10%;
  }
}

@media screen and (max-height: 440px) and (orientation: landscape) {
  .firstlastcontainer{
height:25%;
  }
}

.first {
  width: 46%;
  height: 100%;
  background-color: rgb(25, 25, 25);
  border: 3px solid aliceblue;
  font-family: "Kode Mono";
  color: aliceblue;
  border-radius: 5px;
}
.first:focus {
  outline: none ;
  box-shadow: 0 0 7px 3px rgb(255, 255, 255, 0.5),
  /* inner white */ 0 0 12px 7px rgb(245, 245, 245, 0.4),
  /* middle magenta */ 0 0 17px 11px rgb(235, 235, 235, 0.3);
}
.last {
  width: 46%;
  height: 100%;
  background-color: rgb(25, 25, 25);
  border: 3px solid aliceblue;
  color: aliceblue;
  right: 0;
  font-family: "Kode Mono";
  border-radius: 5px;
}

.last:focus {
  outline: none ;
  box-shadow: 0 0 7px 3px rgb(255, 255, 255, 0.5),
  /* inner white */ 0 0 12px 7px rgb(245, 245, 245, 0.4),
  /* middle magenta */ 0 0 17px 11px rgb(235, 235, 235, 0.3); /* outer cyan */
}

.email {
  width: 100%;
  height: 13%;
  margin-top: 4%;
  background-color: rgb(25, 25, 25);
  border: 3px solid aliceblue;
  color:aliceblue;
  font-family: "Kode Mono";
  border-radius: 5px;
}

@media screen and (max-height: 440px) and (orientation: landscape) {
  .email{
height:25%;
  }
}

.email:focus {
  outline: none ;
  box-shadow: 0 0 7px 3px rgb(255, 255, 255, 0.5),
  /* inner white */ 0 0 12px 7px rgb(245, 245, 245, 0.4),
  /* middle magenta */ 0 0 17px 11px rgb(235, 235, 235, 0.3);/* outer cyan */
}

.message {
  width: 100%;
  margin-top: 4%;
  background-color: rgb(25, 25, 25);
  color: aliceblue;
  border: 3px solid aliceblue;
  font-family: "Kode Mono";
  font-size: 0.9rem;
  text-indent: 1rem;
  padding-top: 10px;
  border-radius: 5px;
}

.message:focus {
  outline: none ;
  box-shadow: 0 0 7px 3px rgb(255, 255, 255, 0.5),
  /* inner white */ 0 0 12px 7px rgb(245, 245, 245, 0.4),
  /* middle magenta */ 0 0 17px 11px rgb(235, 235, 235, 0.3); /* outer cyan */
}
.button {
  width: 20%;
  height: 10%;
  margin-top: 2.6%;
  background-color: rgb(25, 25, 25);
  color: aliceblue;
  border: 3px aliceblue solid;
  font-size: 0.9rem;
  font-family: "Kode Mono";
  border-radius: 5px;
}

@media screen and (max-width:768px) {
  .button{
    width:100%;
  }
}

@media screen and (max-height: 440px) and (orientation: landscape) {
  .button{
height:25%;
  }
}

.button:hover {
  cursor: pointer;
  background-color: aliceblue;
  color: black;
  font-weight: bold;
}

.underlay1 {
  height: 70%;
  width: calc( 400px + 10vw );
  max-width:65%;
  position: absolute;
  margin-top: 10%;
  background-color: black;
  border: 4px solid aliceblue;
  border-top-left-radius: 10px;
  border-top-right-radius: 65px;
  border-bottom-left-radius: 65px;
  border-bottom-right-radius: 10px;

  display: flex;
  justify-content: center;
  align-items: center;
  min-height: 28rem;
}

@media screen and (max-width:768px) {
  .underlay1{
    width:90%;
    height:75%;
    max-width:90%;
  }
}

@media screen and (max-width:440px) and (min-height:742px) {
  .underlay1{

    height:60%;
  }
}

@media screen and (max-height: 440px) and (orientation: landscape) {
  .underlay1{
    height:80%;
    min-height:1px;
    margin-top:7%;
  }
}


.underlay1:hover {
  box-shadow: 0 0 7px 3px rgb(255, 255, 255, 0.5),
  /* inner white */ 0 0 12px 7px rgb(245, 245, 245, 0.4),
  /* middle magenta */ 0 0 17px 11px rgb(235, 235, 235, 0.3); /* outer cyan */
}

.underlay2 {
  margin-top: 7.5%;
  margin-left: 0%;
  height: 80%;
  width: 50%;
  background-color: black;
  color: aliceblue;
  top: 0;
  position: absolute;
  z-index: 99;
}

.hidden {
  visibility: hidden;
  position: absolute;
}

@media screen and (max-width:768){
  .particles{
    visibility: hidden;
    height: 0;
    width: 0;
  }
}

.particles2{
  visibility: hidden;
  height: 0;
  width: 0;
}
@media screen and (max-width:768){
  .particles2{
    visibility: visible;
  }
}
@keyframes top-down {
  0% {
    top: -1vw;
    opacity: 0%;
  }
  25% {
    opacity: 25%;
  }
  50% {
    opacity: 50%;
  }
  75% {
    opacity: 75%;
  }

  100% {
    top: 5vw;
    opacity: 100%;
  }
}

@keyframes fade-in {
  0% {
    opacity: 0%;
  }
  25% {
    opacity: 25%;
  }
  50% {
    opacity: 50%;
  }
  75% {
    opacity: 75%;
  }

  100% {
    opacity: 100%;
  }
}
